import { introAdmin } from '../../config'
export default {
	data() { //引入PopupPicker组件 引入提现路由
		return {
			popupArr: [
				[]
			],
			moneyArr: [],
			DrId: this.$store.state.userInfo == null ? 18 : this.$store.state.userInfo.Data.User.DoctorId,
			timeFn: [],
			AvailableIncome: 0, //可提现
			UnconfirmIncome: 0, //待确定
			TotalIncome: 0, //累计收入
			MonthIncome:0,//本月收入
			UnCashIncome:0,//我的收入
			moneyCss: {
				width: ''
			},
			type: Boolean,
			default: false,
			scrollStart: false,
			showIncome:this.$store.state.isShowIncome,//默认隐藏
			introAdmin:introAdmin
		}
	},
	created() {
		//获取显示隐藏金额
		let _this = this
		_this.$bridge.registerhandler('GetHideIncomeState', (data, responseCallback) => {
			//收入金额的显示隐藏，0  默认显示   1  隐藏
            const state = JSON.parse(data)
            if(state && (state.incomeState == 1 || state.incomeState == "1")){
            	_this.showIncome = 1
            }else{
            	_this.showIncome = 0
            }
            responseCallback("已接收数据")
        })
		let date = new Date
		const year = date.getFullYear()
		this.timeFn[0] = year
		const long = year - 2018
		for(let i = 0; i <= long; i++) {
			this.popupArr[0].push(year - i)
		}
		this.incomeFn();
		this.incomeYearFn(this.timeFn[0]);
	},
	beforeRouteLeave(to,from,next){
		window.onscroll = null
		this.$store.dispatch('pushIsShowIncome', this.showIncome)
		next()
	},
	methods: {
		back() {
			this.$store.dispatch('pushcMed', null)
			const now = location.href.split('?')[0]
			const start = this.$store.state.startUrl
			if(now === start) {
				const data = '123'
				this.$bridge.callhandler('Back', data)
			} else {
				this.$router.back()
			}
		},
		onChange(value) { //获取时间组件数据
			this.$refs.scroll.scrollTo(0, 0)
			this.incomeYearFn(value[0])
		},
		isShowFun(){
			//收入金额的显示隐藏，0  默认显示   1  隐藏
			if(this.showIncome == 1){
				this.showIncome = 0
			}else{
				this.showIncome = 1
			}
			let obj = {
            	incomeState:this.showIncome
            }
            this.$bridge.callhandler('IsHideIncome', JSON.stringify(obj))
            //this.$bridge.registerhandler('IsHideIncome', JSON.stringify(obj))
		},
		IncomeRecordFn() {
			//收入记录
			this.$router.push({
				path: '/Center/IncomeRecord',
				name: 'IncomeRecord',
				params: {
					id: this.DrId,
				}
			})
		},
		DepositRecordFn() {
			//提现记录
			this.$router.push({
				path: '/Center/depositRecord',
				name: 'DepositRecord',
				params: {
					id: this.DrId,
				}
			})
		},
		cardFn() {
			this.$router.push({
				path: '/Center/card',
				name: 'card',
				params: {
					id: this.DrId,
				}
			})
		},
		incomeFn() { //获取可提现、待确定、总金额
			let _date = new Date()
			let InYear = _date.getFullYear()
			let Month = _date.getMonth() + 1
			this.$get('/StatisticAPI/IncomeActive/QueryDoctorInSummaryByDrId', {
				DrId: this.DrId,
				Month:Month
			}).then(o => {
				if(o != null && o != undefined && o != []) {
					this.MonthIncome = o.MonthIncome
					this.UnCashIncome = o.UnCashIncome
					this.TotalIncome = o.TotalIncome
				}
			})
		},
		incomeYearFn(t) { //根据年份获取收入
			let arr = [{
					Month: 1,
					MonSum: 0.0
				},
				{
					Month: 2,
					MonSum: 0.0
				},
				{
					Month: 3,
					MonSum: 0.0
				},
				{
					Month: 4,
					MonSum: 0.0
				},
				{
					Month: 5,
					MonSum: 0.0
				},
				{
					Month: 6,
					MonSum: 0.0
				},
				{
					Month: 7,
					MonSum: 0.0
				},
				{
					Month: 8,
					MonSum: 0.0
				},
				{
					Month: 9,
					MonSum: 0.0
				},
				{
					Month: 10,
					MonSum: 0.0
				},
				{
					Month: 11,
					MonSum: 0.0
				},
				{
					Month: 12,
					MonSum: 0.0
				},
			]
			this.$get('/StatisticAPI/IncomeActive/QueryDoctorMonSummaryByDrId', {
				DrId: this.DrId,
				InYear: t
			}).then(o => {
				this.moneyArr = []
				this.scrollStart = true
				this.moneyCss.width = (o.length * 136.8) / 75 + 'rem'
				o.forEach((n, k) => {
					arr.forEach((v, i) => {
						if(v.Month == n.Month) {
							arr[i] = n
						}
					})
				})
				this.moneyArr = arr;
				let date = new Date
				const year = date.getFullYear()
				const month = date.getMonth()
				setTimeout(() => {
					if(this.timeFn[0] == year) {
						if(month >= 4) {
							this.$refs.scroll.scrollTo((month - 4) * 80, 0)
						}
					}
				}, 100)
			})
		},
		NumFormat(m) { //获取金额 3位添加逗号
			if(m == 0) {
				return '0.00'
			} else {
				return m
			}
			if(!m) return '0.00';
			return Number(m).toFixed(0).toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
		},
		routerLinkFn() {
			this.$router.push({
				path: '/Center/deposit',
				name: 'deposit',
				params: {
					id: this.DrId,
				}
			})
		},
		goIntro(){
			//通知APP打开服务介绍
			let _url = this.introAdmin + "index.html#/incomeIntro?fromAPP=1"
			let obj = {
				title : "服务介绍",
				linkUrl : _url
			}
			this.$bridge.callhandler('ServiceIntroduction', JSON.stringify(obj))
		}
	}
}



